

.session-control-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 80%;
	justify-content: flex-start;
	height: 100%;
	padding: 20px;
	gap: 20px;

	.gameroom-container {
		width: auto;
		height: auto;
	}
	
	
	.campaign-display {
		justify-content: flex-end;
		
		.campaign-info {
			width: 100%;
			font-size: 1.4em;
			font-weight: bold;
			&.hooks {
				font-weight: normal;
				font-size: 1.0em;
			}
		}
	}
	
	.session-control-campaign{
		width: 650px;
		z-index: 2;;
	}
	.encounter-overview {
		background-color: #000000cc;
		border: 4px solid #ffffff44;
		border-radius: 15px;
		height: 100%;
		width: 100%;
	}

}

.encounter-overview-container {
    display: flex;
    
    width: 100%;
    height: 100%;
	gap: 20px;
    position: relative;
    align-items: center;
	flex-direction: column;
	justify-content: center;
	.encounter-overview-nonmodal {
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: space-around;
		display: flex;
		flex-direction: column;
	}
	.encounter-overview-modal {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1; // Put behind other content
	}
    .encounter-icon-container {
		
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 0;  // Put behind other content
		
		.cimage {
			width: 100%;
			height: 100%;
			opacity: 0.1;
			object-fit: contain;  // Maintain aspect ratio
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			object-fit: cover;
		}
	}
    .encounter-overview-contents {
       display: flex;
	   flex-direction: row;
	   align-items: center;
	   justify-content: center;
	   z-index: 2;
	   width: 100%;
	   .encounter-overview-info-column {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			width: 550px;
			
			
			padding-bottom:8px;
			.fill-bar {
				margin-bottom: 4px;
				.fill-bar-label {
					color: var(  --encounter-overview-main-text);
				}
				.fill-bar-container {
					width: auto;
					margin-bottom: 4px;
					margin-top: 4px;
				}
			}
	   
			.encounter-header {
				color: rgba(228, 228, 228, 0.948);
				font-size: 2.8em;
				font-weight: bold;
				position: relative;
				
				.more-info{
					//position: relative;
					
					top: -12px;
					width: 30px;
					height: 30px;
					font-size: .7em;
					color: rgb(255, 255, 255);
					line-height: 1em;
					&.encounter{
						left: -40px;
					}
					&.location{
						right: -40px;
					}
					
				}
			}
		}
    }
	.campaign-display {
		width: 80%;
		position: absolute;
		&.header {
			position: relative;
		}
		z-index: 9;
	}
}

 // Set the Scene
 .encounter-overview-encounter-info{
	display: flex;
	width: 90%;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	line-height: 2.5em;
	width: 100%;
	
	.fill-bar {
		
		white-space: nowrap;
		font-size: 1.5em;
		
 }
	.encounter-overview-encounter-info-tags-row{
		display: flex;
		flex-direction: row;
		gap: 8px;
	}
}

.encounter-overview-encounter-info-tags, .encounter-overview-upcoming-branch-tags {
	display: flex;
	flex-direction: row;
	gap: 10px;
	flex-wrap: wrap;
	justify-content: center;
	padding: 0px;
	margin-bottom: 4px;
	.encounter-flavor-tag {
		padding: 0px 10px;
		font-size: 1.3em;
		margin:0px;
		position: relative;
		&.large-text{
			font-size: .9em;
			font-weight:600;
		}
		&.text-pill {
			padding: 0;
			padding-left: 10px;
			padding-right: 10px;
			&.type-foreshadowing{
				color: white;
			}
		}
	}
}

.encounter-overview-event-header{
	margin-top: 10px;
	font-size: 2em;
	font-weight: bold;
	margin-bottom: 4px;
}
.encounter-overview-events-row {
	display: flex;
	flex-direction: row;
	gap: 8px;
	justify-content: space-around;
	//align-items: center;
	width: 100%;
	margin-bottom: 4px;
}

.encounter-eventlist {
    display: flex;
    flex-direction: row;
	flex-wrap: wrap;
    gap: 20px;
   
	z-index: 2;
	justify-content: center;
	margin-bottom: 10px;
    .event-entry {
        min-width: 430px;
		max-width: 400px;
        padding: 10px;
		line-height: .81em;;
       
        border-radius: 15px;
		position: relative;
        cursor: pointer;
        transition: all 0.2s ease;
		
		font-size: 1.2em;
		align-items: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		border: 4px solid var(--encounter-overview-interactable-border);
        background-color: var(--encounter-overview-interactable);
        &:hover {
			transform: scale(1.04);
			background-color: var(--encounter-overview-interactable-hover);
        }

		&.roleplay{
			background-color: var(--encounter-overview-interactable_roleplay);
			border: 4px solid var(--encounter-overview-interactable-border_roleplay);
			&:hover {
				background-color: var(--encounter-overview-interactable-hover_roleplay);
			}
		}
		&.challenge{
			background-color: var(--encounter-overview-interactable_challenge);
			border: 4px solid var(--encounter-overview-interactable-border_challenge);
			&:hover {
				background-color: var(--encounter-overview-interactable-hover_challenge);
			}
		}
		&.combat{
			background-color: var(--encounter-overview-interactable_combat);
			border: 4px solid var(--encounter-overview-interactable-border_combat);
			&:hover {
				background-color: var(--encounter-overview-interactable-hover_combat);
			}
		}

		&.perception{
			background-color: var(--encounter-overview-interactable_perception);
			border: 4px solid var(--encounter-overview-interactable-border_perception);
			&:hover {
				background-color: var(--encounter-overview-interactable-hover_perception);
			}
		}
		&.town{
			background-color: var(--encounter-overview-interactable_town);
			border: 4px solid var(--encounter-overview-interactable-border_town);
			&:hover {
				background-color: var(--encounter-overview-interactable-hover_town);
			}
		}

		&.building{
			background-color: var(--encounter-overview-interactable_building);
			border: 4px solid var(--encounter-overview-interactable-border_building);
			&:hover {
				background-color: var(--encounter-overview-interactable-hover_building);
			}
		}


		&.completed {
			color: #7e7e7eb6;
			border: 4px solid var(--encounter-overview-interactable-border_inactive);
			background-color: var(--encounter-overview-interactable_inactive);
			cursor: default;;
			pointer-events: none;
			.event-icon {
				opacity: 0.5;
			}
			.event-flavor-text{
				color: #7e7e7eb6;
				
			}
			&:hover {
				transform: scale(1.04);
				background-color: var(--encounter-overview-interactable-hover_inactive);
			}
		}



        .event-name {
            font-size: 1.6em;
            font-weight: bold;
            margin-bottom: 10px;
        }

        .event-description {
            font-size: 0.9em;
            color: #cecece;
            margin-bottom: 8px;
			
        }

        .event-flavor-text {
            font-size: 1.0em;
			line-height: 1em;
            font-style: italic;
            color: #ffffff;
			width: 80%;
        }
		.event-icon {
			width: 40px;
			height: 40px;
			background-color: white;
			border-radius: 100%;
			padding:2px;
			line-height:1em;
			position: absolute;
			top: 5px;
			left: 5px;
			.cimage {
				width: 80%;
				height: 80%;
				top: 10%;
				position: relative;
				object-fit: cover;
			}
		}
    }
}

