						color: white;
					}
					// .encounter-overview-upcoming-travel-location{
					// 	background-color: #ffffffb3;
					// 	border-radius: 15px;
					// 	width: 80%;
					// 	font-size: 1.2em;
					// 	font-weight: bold;
					// 	margin-top: 6px;
					// }
				}
			}
