	}
	.encounter-overview-advance-container{
		font-size: 2em;
		font-weight: bold;
		z-index: 2;
		.encounter-overview-advance-text{

		}
	    .encounter-overview-advance-button{
			background-color: var(--encounter-overview-interactable);
			height: 142px;
			border-radius: 20px;
			width: 450px;
			align-items: center;
			justify-content: center;
			display: flex;
			border: 4px solid var(--encounter-overview-interactable-border);
			flex-direction: column;
			cursor: pointer;
			pointer-events: all;
			transition: all 0.2s ease;
			&:hover {
				transform: scale(1.05);
				background-color: var(--encounter-overview-interactable-hover);
			}
			.encounter-overview-advance-button-text{
				font-size: 1.3em;
				margin-bottom: 8px;
			}
			.encounter-overview-advance-info{
				font-size: .7em;
				margin-left: 10px;
				line-height: 1em;
				font-weight: bold;
				color: #ffffffb3;
			}
		}   
	}
}

.encounter-overview-legend{
	
	height: 100%;
	background-color: #ffffff2e;
	border-radius: 20px;
	border: 4px solid #ffffffb3;
	padding: 10px;
	display: flex;
	flex-direction: column;
	gap: 6px;
	margin-left: 20px;
	.event-legend-entry{
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		width: 250px;
		.event-icon {
			width: 24px;
			height: 24px;
			background-color: white;
			border-radius: 100%;
			padding:2px;
			line-height:1em;
			margin-right: 10px;
			top: 5px;
			left: 5px;
			.cimage {
				width: 80%;
				height: 80%;
				top: 10%;
				position: relative;
				object-fit: cover;
			}
		}
		.event-name {
			font-size: 1.2em;
			font-weight: bold;
			margin-right: 10px;
		}
	}
}

.encounter-overview-break {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 160px;
	height: 160px;
	background-color: var(--encounter-overview-interactable);
	border-radius: 100%;
	border: 4px solid var(--encounter-overview-interactable-border);
	position: absolute;
	top: 20px;
	right: 20px;
	cursor: pointer;
	pointer-events: all;
	transition: all 0.2s ease;
	z-index: 2;
	&.hidden{
		display: none;
	}
	&:hover{
		transform: scale(1.05);
		background-color: var(--encounter-overview-interactable-hover);
	}
	.encounter-overview-break-label{
		font-size: 1.5em;
		font-weight: bold;
	}

	.encounter-overview-break-effect {
		font-size: .9em;
		font-weight: normal;
		width: 80%;
	}
}