                    position: relative;
                    z-index: 99;
                    .needle-gauge {
                        position: absolute;
                        display: flex;
                        width: 156px;
                    }
                }
                .stress-label-containers {
                    display: flex;
                    margin-top: 30px;
                    flex-direction: row;
                    justify-content: space-between;
