    .gameroom-table-row-container{
        display: flex;
        width: 100%;
        height: 100%;;
        flex-direction: column;
        justify-content: space-around;
        .gameroom-table-row {
            display: flex;
            flex-direction: row;
            position: relative;
        
            height: 100%;
            margin-top: 8px;
        

            .game-table-container {
            
                width: 100%;
            
                display: flex;

                .game-table-slab {
