.encounter-icon-container {
		
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;  // Put behind other content
    
    .cimage {
        width: 100%;
        height: 100%;
        opacity: 0.1;
        object-fit: contain;  // Maintain aspect ratio
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        object-fit: cover;
    }
}


.bottom-bar-controls {
    display: flex;
    
    margin-right: 0px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
}

.gameroom-nav-button-container {
  
    height: 80px;
    margin: 4px;
    margin-left: 4px;
    font-size: .8em;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
gap: 10px;
    .gameroom-nav-button {
        width: 80px;
        height: 80px;;
        box-sizing: border-box;
        background-color: rgb(54, 66, 100);
        border-color: rgb(37, 37, 60);
        margin-top: 0;
