.table-encounter-container {
    display: flex;
    flex-direction: column;
    
    border-radius: 12px;
    border: 5px solid #000000;
   
    font-size: 3em;
    font-weight: bold;
    width: 100%; 
    
    position: relative;

    
    transition: 1s;
    position:relative;
    overflow: hidden;
    .table-encounter-scene {
        height: 100%;
        .table-encounter-title {
            display: none;
        }
    }
    .encounter-name {
        font-size: 1.3em;
        font-weight: bold;
        position: relative;
    }

    &.forest {
        color: var(--forest-display-pen);
        border-color: var(--forest-display-pen);
     
    }

    &.town { 
        color: var(--town-display-pen);
        border-color: var(--town-display-pen);
       
    }
    

    &.building { 
        color: var(--town-display-pen);
        border-color: var(--town-display-pen);
       
    }

    &.mountain {
        color: var(--mountain-display-pen);
        border-color: var(--mountain-display-pen);
      
    }

    &.dungeon {
        color: var(--dungeon-display-pen);
        border-color: var(--dungeon-display-pen);
      
    }

    &.water {
        color: var(--water-display-pen);
        border-color: var(--water-display-pen);
       
    }

    .table-encounter-divisions {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border: 2px solid green;
        height: 90%;
        width: 100%; 
        .event-column{
            border: 2px solid blue;
            display: flex;
            flex-direction: column;
            flex: 1 1 0;
            justify-content: center;
            gap: 40px;
            padding: 10px;
            .event-column-label {
               
                margin-top: auto;
            }
            .encounter-event {
                            
               
                position: relative;
                margin: 8px;
                padding: 12px;
                margin-top: auto;
                border: 4px solid #ccc;
                border-radius: 24px;
                overflow: hidden;
                
                .event-controls {
                   width: 100%;
                   align-content: center;
                   justify-content: center;
                   display: flex;
                    .move-button {
                        width: 75%;
                        position: absolute;
                        height: 25%;
                        padding: 0;
                        transition: .5s;
                      
                        background: #ffffff33;
                        cursor: pointer;
                        
                        &.up {
                            top: 0;
                        }

                        &.down {
                            bottom: 0;
                        }

                        &:hover {
                            background: #ffffff55;
                        }

                        &.left {
                            left: 0;
                        }
                        &.right {
                            right: 0;
                        }
                    }

                    .swap-mid-button {
                        width: 25%;
                        height: 100%;
                        padding: 0;
                        transition: .5s;
                        background: #ffffff33;
                        top: 0;
                        cursor: pointer;
                        position: absolute;
                        
                        &:hover {
                            background: #ffffff55;
                        }

                        &.left {
                            
                            right:0;
                        }
                        &.right {
                         
                            left:0;
                        }
                    }
                }
            }
        }
    }
    .table-encounter-session {
        position: relative;
        width: 100%;
        height: 100%;
       // top: -100%;
        display: flex;
        align-items: center;
        .encounter-intro-container{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 100%;
            width: 100%;
            position: relative;
       
            .encounter-intro-title{
                display: flex;
                font-size: 1.6em;
           
                margin-top: 0px;
                margin-bottom: 30px;
            }
            .encounter-intro-location{
                display: flex;
                position: relative;
                font-size: 1.2em;
            }
            .encounter-intro-instructions{
                display: flex;
                position: relative;
                font-size: .6em;
                line-height: 1em;
                &.expect{
                    margin-bottom: 10px;
                }
                &.plot{
                    margin-top: 10px;
                    margin-bottom: 10px;
                    line-height: normal;
                }
                &.dm-tricks{
                    margin-top: 10px;
                    margin-bottom: 10px;
                }
            
            }
            .encounter-flavor-tag {
                position: relative;
            }
            .encounter-intro-tags {
                display: flex;
                flex-direction: row;
                margin-bottom: 20px;
                
                &.plot {
                    margin-top: 10px;
                    line-height: normal;
                }
            }
          
        }
    }

    .encounter-roleplay-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;;
        position: relative;
        .encounter-roleplay-header{
            display: flex;
            flex-direction: row;
            width: 100%;
            justify-content: space-around;
            align-items: center;
            > * {
                max-width:33.3%;
            }
            .encounter-roleplay-instructions{
                display: flex;
                position: relative;
                font-size: .5em;
                 
            }
            .encounter-roleplay-tutorial{
               background-color: #00000066;
               font-size: .4em;
               color: white;
               padding: 0px;
               margin-top: 8px;
               margin-bottom: 4px;
               border-radius: 10px;
               border: 6px solid #00000036;
               width: 100%;

               max-height: 200px;
               overflow: hidden;
               .sample-tag {
                    height: 15px;
                    margin-left: 4px;
                    top: 0px;
               }

            }
            .encounter-roleplay-text {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }
            .encounter-roleplay-skill-container{
                padding: 20px;
                font-size: .51em;
                min-width: 250px;
                
                .fill-bar-label {
                    margin-bottom: 2px;
                }
                .fill-bar-container {
                    height: 40px;
                    border: 3px solid #00000044;
                }
                .encounter-roleplay-hurry-button{
                    background-color: #eaeaea;
                    border-radius: 10px;
                    border: 4px solid #00000044;
                    margin-top: 10px;
                    font-size: .8em;
                    padding-top: 2px;
                    height: 40px;
                    width: 100%;
                    box-sizing: border-box;
                    transition: .3s all;
                    cursor: pointer;
                    &:hover{
                        background-color: #e8e8e844;
                        color: rgb(255, 255, 255);
                        transform: scale(1.05);
                    }
                    &.disabled {
                        opacity: .5;
                        cursor: default;
                        pointer-events: none;
                    }

                }
                .encounter-roleplay-hurry-text {
                    font-size: .5em;
                
                    margin-bottom: 10px;
                    color: white;
                }
            }

        }
        .encounter-roleplay-token-display {
            position: absolute;
            right: 0;
            top: 0;
            font-size: .5em;
            margin: 20px;
        }
        .encounter-roleplay-title{
            display: flex;
            position: relative;
            font-size: .8em;
        }
       
        .encounter-roleplay-flavor-tags-container {
            position: relative;
            height: 100%;
            width: 100%;
            &.disabled {
                &::before,
                &::after {
                  opacity: 0;
                }
              }
            // Add horizontal bands
            &::before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              background: 
                linear-gradient(
                  to bottom,
                  transparent 0%,
                  rgba(2, 218, 128, 0.05) 0%,
                  rgba(2, 218, 128, 0.3) 35%,
                
                  transparent 35%,
                  rgba(255, 255, 255, 0.05) 36%,
                  rgba(255, 255, 255, 0.2) 70%,
                  transparent 70%,
                   rgba(118, 0, 0, 0.051) 71%,
                  rgba(118, 0, 0, 0.3) 100%,
                  transparent 90%
                );
              pointer-events: none;
              transition: opacity 0.3s ease;
            }
          
            // Add horizontal lines
            &::after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              background: 
                linear-gradient(
                  to bottom,
                  rgba(255, 255, 255, 0.2) 0%,
                  transparent 2%,
                  transparent 35%,
                  rgba(255, 255, 255, 0.2) 35%,
                  transparent 37%,
                  transparent 70%,
                  rgba(255, 255, 255, 0.3) 70%,
                  transparent 72%
                );
              pointer-events: none;
              transition: opacity 0.3s ease;
            }
            .band-label {
                position: absolute;
                left: 10px;
                
                transform: translateY(-50%);
                color: rgba(255, 255, 255, 0.9);
                font-size: 0.5em;
                
                pointer-events: none;
               // text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
                z-index: 0;
                &.disabled {
                    opacity: 0.0;
                    
                  }
              }
          }
          
        
    }
}


.encounter-flavor-tag {
    position: absolute;
    padding: 5px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    transition: 0.2s;
    color: white;
    font-size: .75em;
  
   
    &:hover .tooltip-text {
      opacity: 1; // Show the tooltip on hover
    }


    &.text-pill {
        padding: 10px;
        padding-top: 5px;
        border-style: solid;
    }
    
}
 .tooltip-text {
      position: absolute;
      bottom: 100%; // Position the tooltip above the element
      left: 50%;
      transform: translateX(-50%);
      background-color: rgba(48, 48, 48, 0.8);
      color: white;
      padding: 5px 10px;
      border-radius: 5px;
      white-space: nowrap;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.2s;
      z-index: 10;
      font-size: .7em;
      text-overflow: wrap;
      width: 100%;
      white-space: normal;
      width: 300px;
    }
  
.encounter-rp-character {
    position: absolute;
    padding: 5px 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    transition: .5s;
    font-size: .75em;
    
    &:hover {
     
      transform: scale(1.05);

      .tooltip-text {
        opacity: 1; // Show the tooltip on hover
      }
    }
  
    .cimage {
        width: 60px;
        height: 60px;
        border-radius: 10px;
        display: flex;
    }
    .encounter-rp-character-name {
        font-size: .75em;
        font-weight: bold;
        color: rgb(177, 200, 223);
    }

    .encounter-rp-character-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        .encounter-rp-icon-row{
            display: flex;
            flex-direction: row;
            gap: 10px;
            margin-top: 0px;
            line-height: .9em;
            align-items: center;
            justify-content: center ;
        }
    }
}


@keyframes glow {
    0% {
      box-shadow: 0 0 5px var(--glow-color), 0 0 10px var(--glow-color), 0 0 15px var(--glow-color), 0 0 20px var(--glow-color);
    }
    50% {
      box-shadow: 0 0 10px var(--glow-color), 0 0 20px var(--glow-color), 0 0 30px var(--glow-color), 0 0 40px var(--glow-color);
    }
    100% {
      box-shadow: 0 0 5px var(--glow-color), 0 0 10px var(--glow-color), 0 0 15px var(--glow-color), 0 0 20px var(--glow-color);
    }
  }

  @keyframes glowless {
    0% {
      box-shadow: 0 0 2px var(--glow-color), 0 0 5px var(--glow-color), 0 0 8px var(--glow-color), 0 0 10px var(--glow-color);
    }
    50% {
      box-shadow: 0 0 5px var(--glow-color), 0 0 10px var(--glow-color), 0 0 15px var(--glow-color), 0 0 20px var(--glow-color);
    }
    100% {
      box-shadow: 0 0 2px var(--glow-color), 0 0 5px var(--glow-color), 0 0 8px var(--glow-color), 0 0 10px var(--glow-color);
    }
  }
  
  .theme-comedic {
    border-color: var(--fill-color-theme-comedic);
    --glow-color: var(--fill-color-theme-comedic);
    animation: glow 2s infinite;
    &.muted{
        animation: glowless 2s infinite;
    }
  }
  
  .theme-dramatic {
    border-color: var(--fill-color-theme-dramatic);
