.game-encounter-perception {
    display: flex;
    flex-direction: column;
    align-items: center;
    width:100%;
    transition: .5s;
   
    .encounter-type-title {
        
        background-color: #ffffff33;
        border-radius: 40px;
        
        padding-left:20px;
        padding-right: 20px;
        padding-bottom: 4;
        
        position: absolute;
        top: 20px;
       
    }
    .perception-container {
        display: flex;
      
        margin: 20px;
        border: 8px solid #ffffff44;
        border-radius: 5px;;
        padding: 4px;
        width: 100%;
       
        //changing this will hide the tooltips... hmmm
        overflow: visible;
        max-width: 800px;
        height: calc(100% - 40px);  // Account for margins

        &::-webkit-scrollbar {
            display: none; /* For Chrome, Safari, and Opera */
        }    
        .character-list {
            color: var(--encounter-color-perception);
            display: flex;
            flex-direction: column;
            gap: 10px;
            position: relative;
            width: 100%;
            flex: 1;  // Take up available space
            max-height: 700px;
            min-height: 0;  // Required for Firefox
            overflow-y: auto;
            overflow-x: visible;

            overflow-y: scroll;
            scrollbar-width: none; /* For Firefox */
            -ms-overflow-style: none;  /* For Internet Explorer and Edge */
            .perception-row {
                display: flex;
                flex-direction: row;
                gap: 20px;
               
                border-radius: 10px;
                border: 4px solid #ffffffcc;
                background-color: #cccccc66;
                width: 100%;
                box-sizing: border-box;
                
              
                .character-display {
                    display: flex;
                    
                    flex-direction: column;
                    gap: 1px;
                    align-items: center;
                    width: 100px;
                  
                    
                    .character-icon {
                        width: 75px;
