  .tone-tracker {
    background: rgba(47, 54, 57, 0.123);
    border: 4px solid rgba(234, 233, 233, 0.573);
    border-radius: 8px;
    padding: 2px;
    padding-left: 8px;
    padding-right: 8px;
    
    .tone-header {
      margin-bottom: 8px;
      
      .tone-title {
        font-size: 14px;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 0.05em;
        color: #fff;
      }
      
      .tone-subtitle {
        font-size: 12px;
        color: rgba(255, 255, 255, 0.6);
        margin-left: 8px;
      }
    }
  
    .tone-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);    
        gap: 10px;
        justify-content: center;

       
        .tone-item {
        background: rgba(41, 41, 41, 0.659);
        border-radius: 6px;
        padding: 6px;
        width: 120px;
        height: 12px;
           &:only-child {
           
            margin: 0 auto;  // center horizontally
            grid-column: 1 / -1;  // span all columns
        }
        &.active{
            &.theme-dramatic { background-color: var(--fill-color-theme-dramatic); }
            &.theme-heroic { background-color: var(--fill-color-theme-heroic); }
            &.theme-mysterious { background-color: var(--fill-color-theme-mysterious); }
            &.theme-comedic { background-color: var(--fill-color-theme-comedic); }
            &.theme-serious { background-color: var(--fill-color-theme-serious); }
            &.theme-fantasy { background-color: var(--fill-color-theme-fantasy); }
            &.theme-horror { background-color: var(--fill-color-theme-horror); }
            &.theme-grim { background-color: var(--fill-color-theme-grim); }
           
            .tone-item-header{
                .tone-name{
                    font-size: 1.5em;
                }
            }
           
        }
        .tone-item-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 8px;
          
          .tone-name {
            font-size: 16px;
            font-weight: 500;
            line-height: .1em;
            margin-bottom: 16px;
          }
          
          .tone-count {
            font-size: 12px;
            line-height: .7em;
            opacity: 0.8;
            margin-bottom: 12px;
          }
        }
        
        .tone-progress-container {
          height: 4px;
          top: -16px;
          background: rgba(255, 255, 255, 0.8);
          border-radius: 2px;
          position: relative;
          
          .tone-progress-bar {
            height: 100%;
            border-radius: 2px;
            transition: width 0.3s ease;
            
            &.theme-dramatic { background-color: var(--fill-color-theme-dramatic); }
            &.theme-heroic { background-color: var(--fill-color-theme-heroic); }
            &.theme-mysterious { background-color: var(--fill-color-theme-mysterious); }
            &.theme-comedic { background-color: var(--fill-color-theme-comedic); }
            &.theme-serious { background-color: var(--fill-color-theme-serious); }
             &.theme-fantasy { background-color: var(--fill-color-theme-fantasy); }
             &.theme-horror { background-color: var(--fill-color-theme-horror); }
             &.theme-grim { background-color: var(--fill-color-theme-grim); }
          }
          
          .tone-redeem-button {
            position: absolute;
            right: 0;
            top: -30px;
            background: rgba(255, 255, 255, 0.1);
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: 4px;
            padding: 4px 8px;
            font-size: 12px;
            color: #fff;
            cursor: pointer;
            transition: all 0.2s ease;
            
            &:hover {
              background: rgba(255, 255, 255, 0.2);
              border-color: rgba(255, 255, 255, 0.3);
            }
            
            .redeem-icon {
              margin-right: 4px;
              font-size: 14px;
            }
          }
        }
      }
    }
  }


