.game-encounter-challenge {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
    width: 100%;
   height: 100%;


    .challenge-container {
       display: flex;
       
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: space-between;
        .challenge-top-bar {
            
            
            .encounter-title {
              
                font-size: 1em;
                &.desc {
                    font-size: .5em;
                    padding: 4px;
                   
                } 

            }
        }
        
        .challenge-body{
          
            display: flex;
            flex-direction: row;
         
            .challenge-character-list{
                margin-left: 10px;
                .challenge-encounter-heroes {
                    font-size: 1em;
                }
                
            }
            .challenge-encounter-available-tags-title{
                font-size: .5em;
                color: #e7dddd;
            }
            
            .challenge-display-container{
                width: 100%;
               
                justify-content: center;
                display: flex;
                flex-direction: column;
                align-items: center;
                .challenge-description{
                    border: 2px solid rgb(0, 0, 0);	
                }
            }
            .tags-container{
                font-size: .5em;
                
              
            }
        }
        
        .challenge-footer{
         
           
            display: flex;
            flex-direction: row;
            .challenge-reward-track{
                width: 100%;
                height: 100%;
            }
            .challenge-rewards-ui-container{
               background-color: rgba(71, 19, 18, 0.6);
                width: 300px;
                margin: 4px;
                border: 4px solid rgb(134, 153, 134);
                border-radius: 10px;
                display: flex;
                flex-direction: column;
                align-items: center;    
                gap: 0px;
            }
        }
    }
}

.challenge-location-tag-name {
    cursor: pointer;
    &.expected {
        cursor: default;
    }
    &.type-location {
        &.used {
            text-decoration: line-through;
            opacity: 0.5;
        }
    }
    &.text-pill {
        font-size: 19px;
    }
}



.challenge-encounter-hero-container {
    display: flex;
    flex-direction: row;
    border: 4px solid rgba(33, 54, 33, 0.667);
    background-color: #00000044;
    height: 92px;
    border-radius: 10px;
}

.challenge-encounter-hero {
    background-color: #ffffff;
    display: flex; 
    flex-direction: column;
    font-size: .5em;
   
    height: 100%;
    width: 90px;
    border-radius: 10px;
    box-sizing: border-box;
    align-items: center;
    border: 4px solid #00000099;
    position: relative;
    &.selected {
        background-color: #ffdaa3;
    }
    .challenge-encounter-hero-name {
        width: 80px;
        font-size: .8em;
       
    }
    .challenge-character-icon {
        width: 50px;
        height: 50px;
        border-radius: 5px;
       
        margin-bottom: 4px;
        bottom: 4px;
        box-sizing: border-box;
        
        .cimage {
            position: relative;
            width: 100%;
            height: 100%;
            border-radius: 100%;
        }   
    }

    .challenge-encounter-hero-injury {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(71, 19, 18, 0.6);
        z-index: 2;
        &.dead {    
            background-color: black;
        }
    }
}


.challenge-encounter-hero-attributes{
    margin-left: 4px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;;
    .attribute-display-container{
        left: 0;
       
        align-items: flex-start;
        display: flex;
        flex-direction: column;



        .attribute-display {
            font-size: .4em;
            display: flex;
            flex-direction: row;
            .attribute-title {
               display: none;
            }
            .attribute-container {
                position: relative;
                flex-direction: row;
                display: flex;
                font-size: .95em;
                letter-spacing: -6px;
                flex-wrap:wrap;
              .attributes-lost {
                opacity: 0.2;   
              }
            
            }
        }
    }  
}

.challenge-reward-row {
    display: flex;   
    flex-direction: row;
   
    background-color: #00000044;
               
    margin: 4px;
    border: 4px solid rgb(134, 153, 134);
    border-radius: 10px;
    padding: 4px;
    padding-top: 8px;
}

.challenge-labels {
    justify-content: flex-start;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    gap: 8px;
    margin-top: 18px;
    margin-right: 10px;
    width: 200px;
    color: black;

}
.challenge-bars {
    width: 100%;
    position: relative;
}

.challenge-bar-results-label{
    position: absolute;
    top: 0;
    font-size: 1.5em;
    margin-left: 50px;
    top: -4px;
    color: black;
}

.challenge-bar-container {
    display: flex;
    flex-direction: row;
   
    align-items: center;
    font-size: large;
    
    margin: 6px;
    margin-bottom: 0px;
    width: 100%; 
    display: flex;
    box-sizing: border-box;
   
    position: relative;
    .segment-numbers {
        display: flex;
        justify-content: space-between;
    
        width: 100%;
        .segment-number {
        flex: 1;
        text-align: center;
        font-size: 1.1em;
        color: #1a1515;
        }
    }
  


    .challenge-arrow {
        position: absolute;
        bottom: 100%;
        width: 0;
        height: 0;
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-top: 8px solid rgb(134, 153, 134);
        margin-left: -10px;
        margin-bottom: 6px;
       
      }
    .segment-instance {
        width: 100%;
        margin-bottom: 4px;
        .challenge-bar {
           
            
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
           
            .segment-container {
                display: flex;
                flex-direction: row;
                width: 100%;
                height: 26px;
                
                    .challenge-bar-segment {
                        flex: 1;

                        border: 2px solid rgb(134, 153, 134);
                        
                        border-right: 1px solid #000; // Segment lines
                        transition: background-color 0.3s;
                        color: #e7dddd;
                        
                        width:100%;
                        transition: all 0.1s ease;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        &.highlighted {
                            filter: brightness(1.9);
                            color: black;
                        }
                        &.reward {
                            color: black;
                        }
                      .label {
                        font-size: 1em;
                        
                       
                      }
                        &:last-child {
                            border-right: none; // Remove border from the last segment
                        }
                        &.theme-{
                            background-color: var(--town-display-pencil-light);
                            &.filled, &.dark {
                                background-color: var(--town-display-pencil);
                            }
                        }
                    }
                }
            }
    }
}


.challenge-encounter-hero-ui{
    display: flex;
    height: 100%;
    box-sizing: border-box;
    font-size: .5em;
    
	.challenge-assist-column{
        margin-right: auto;
        display: flex;
        flex-direction: column;
        width: 130px;
        gap: 2px;
        margin: 2px;
		.challenge-assist-button{
            display: flex;
            flex: 1;
            align-items: center;
            justify-content: center;
            border: 4px solid rgba(33, 54, 33, 0.667);
            border-radius: 5px;
            cursor: pointer;
            transition: all 0.1s ease;
            &.assist{
               background-color: #f2efef;
               &.selected {
                    border: 4px solid rgb(255, 255, 255);
                    background-color: #ffdaa3;
                }
            }
            &.location{
                background-color: #f2efef;
                .challenge-use-label {
                   
                    top: 0px;
                    padding-bottom: 2px;
                    border: 2px solid rgb(115, 115, 128);
                    font-size: 19px;
                    &.hidden{
                        display: none;
                    }
                }
            }
            &:hover {
                transform: scale(1.08);
            }
            &.disabled {
                opacity: 0.5;
                cursor: auto;
                pointer-events: none;
                &:hover {
                    transform: none;
                }
            }
        }
    }

}


.challenge-encounter-available-tags {
    font-size: .6em;
    .challenge-location-tags-container{
        display: flex;
        flex-direction: row;
        margin-bottom: 10px;
        gap: 0px;
        justify-content: center;
        margin-left: 0px;
        margin-right: 0px;
        .text-pill {
            border: 2px solid rgba(33, 54, 33, 0.667);
            margin: 4px;
        }
    }
}

.challenge-failure {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 4px;
    height: 70px;
    .challenge-failure-title {
        font-size: .5em;
        color: #b3b3b3;
    }

    .challenge-display-failures{
        position: relative;
        top: -16px;

        .failure-circle {
            display: inline-block;
            width: 16px; /* Adjust size as needed */
            height: 16px; /* Adjust size as needed */
            border: 4px solid #935858; /* Border color for hollow circle */
            border-radius: 50%; /* Make it a circle */
            margin: 0px 5px; /* Adjust spacing as needed */
            background-color: transparent; /* Hollow by default */
        }
        
        .failure-circle.filled {
            background-color: #d61b1b; /* Filled color */
        }
    }
}



.challenge-accept-results {
    font-size: .45em;
    cursor: pointer;
    width: 160px;
    margin-bottom: 4px;
    border: 4px solid rgb(128, 128, 128);
    border-radius: 12px;
    background-color: #f2efef;
    margin-bottom: 5px;
    line-height: 1em;
    transition: all 0.3s ease;
    &:hover {
        transform: scale(1.1);
    }
}

.challenge-display-footer-legend-container {
    color: #b3b3b3;
    margin-bottom: 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .challenge-display-footer-legend-title {
        font-size: .5em;
    }
  
}
  .challenge-display-legend-info {
    color: #b3b3b3;
        margin-top: 4px;
        font-size: .35em;
        display: flex;
        flex-direction: row;
        gap: 10px;
        line-height: 1.1em;
    }

.challenge-display-column {
    display: flex;
    flex-direction: column;
    align-items: center;
}



.challenge-tag-container{
    display: flex;
    flex-direction: row;
    gap: 10px;
    flex-wrap: wrap;
    padding: 10px;
    justify-content: center;

    margin-bottom: 10px;
    .challenge-tag-ui{
        border: 4px solid rgba(33, 54, 33, 0.667);
        border-radius: 50px;;
        width: 50px;
        height: 34px;
        top: 10px;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #f2efef;
        transition: all 0.3s ease;
        cursor: pointer;
        &:hover {
            transform: scale(1.1);
        }
        .challenge-tag-ui-text{
            margin-bottom: 10px;
        }
    }



    .challenge-current-tag{
        border: 2px solid rgba(33, 54, 33, 0.667);
        border-width: 4px;
        color: #b3b3b3;

        &.text-pill {
            font-size: 1.1em;
            border-radius: 40px;
            height: 30px;
            line-height: 20px;
            padding: 10px;
            margin-bottom: 0px;
          }
    
    }
}



.challenge-display {
    display: flex;
    flex-direction: column;
    border: 4px solid rgba(33, 54, 33, 0.667);
    border-radius: 10px;
    background-color:rgba(0, 0, 0, 0.2666666667);
    align-items: center;
    box-sizing: border-box;
    max-width: 700px;
    width: 100%;
    max-height: 400px;
    margin-bottom: 10px;
  
    .challenge-tag-container {
    

      .text-pill {
        font-size: .8em;
        margin-top: 0px;
        height: 8px;
        line-height: 1px;
        padding: 10px;
        margin-bottom: 6px;
      }

     
     border-bottom: none; 
      .challenge-current-tag-container {
        
        .challenge-current-tag {
            border: 2px solid rgba(33, 54, 33, 0.667);
            border-width: 4px;
            color: #b3b3b3;
        }
      }
    }
    
    .challenge-difficulty-container {
        display: flex;
        flex-direction: row;
        width: 100%;
        
        align-items: center;
      
        border-radius: 5px;
    
        box-sizing: border-box;
        border: 4px solid rgba(33, 54, 33, 0.667);
      
       

      .challenge-difficulty-display-column {
       
        font-size: .5em;
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
        justify-content: center;
        gap: 20px;
        line-height: .9em;
        color: #b3b3b3;
    
     
      
       
      }
    }
    .challenge-difficulty-display-difficulty {
        gap: 40px;
        display: flex;
        width: 100%;
        justify-content: center;
        flex-direction: row;
        font-size: 1.3em;
}
    .challenge-tag-attribute-container { 
       
        display: flex;
        flex-direction: row;
        justify-content: center;
        font-size: 1.5em;
       
        margin-top: 0px;
        margin-left: 10px;
        margin-right: 10px;
        line-height: 1.15em;
        flex-wrap: wrap;
        
    }
  
   
  
  
}
.challenge-difficulty-ui {
    border: 4px solid rgba(33, 54, 33, 0.667);
    border-radius: 5px;;
    width: 50px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f2efef;
    box-sizing: border-box;
    transition: all 0.3s ease;
    cursor: pointer;
    .challenge-difficulty-ui-text {
        margin-bottom: 10px;;
    }
    &.disabled {
        opacity: 0.5;
    }
    &:hover {
        transform: scale(1.1);
    }
   
  
}

.challenge-display-row {
    display: flex; 
    flex-direction: row;
    
    justify-content: space-between;
    width: 100%;
  .challenge-display-header-column{
    font-size: .4em;
    color:  #b3b3b3;
    width: 100%;
  }
  
}


.challenge-succeeded {
    font-size: 1em;
    color: #d2d2d2;
}

.challenge-succeeded-instructions {
    font-size: .5em;
    color: #d2d2d2;
}


.challenge-assistance-container {
    
    .challenge-assistance-list {
      display: flex;
      flex-direction: row;
     
      font-size:50px;
      width: 100%;
      gap: 2px;
      .challenge-assistance-list-column {
        
          display: flex;
          flex-direction: column;
          > div:nth-child(2n) {  
              background-color: #00000055; 
          }
          font-size: 1em;
          .challenge-assistance-list-no-tags{
              font-size: .4em;
              color:  #b3b3b3;
          }
      }

      .attribute-icon {
          height: 10px;
          margin-top: 7px;
          font-size: .7em;
      }
    
      .challenge-assistance-hero {
        display: flex;
        flex-direction: row;
        font-size: .5em;
        justify-content: flex-start;
        text-align: center;
        &.no-hero {
          color:  #b3b3b3;
          font-size: .4em;
        }
        .challenge-assistance-hero-name { 
          border: 4px solid rgb(128, 128, 128);
          border-radius: 5px;
          background-color: #f2efef;
          font-size: 18px;    
          min-width: 82px;
          text-align: center;
          display: flex;
          justify-content: center;
          align-items: center;
          box-sizing: border-box;
          margin-right: 10px;
          
          
        }
      }

      .challenge-assistance-tag {
          
          display: flex;
          flex-direction: row;
          font-size: .45em;
          .challenge-assistance-tag-name-container {
              width: 108px;
              
              display: flex;
              align-items: center;
              justify-content: center;
              margin-bottom: 1px;
              margin-top: 1px;
              height: 31.5px;
              .attribute-icon {
                  display: none;;
              }
              .challenge-assistance-tag-name { 
                  border: 2px solid rgba(33, 54, 33, 0.667);
                  top: 0px;
                  border-bottom: none;
                
                  line-height: 20px;
                  padding-bottom: 0px;
              }
          }
      }
    }
  }

.challenge-display-footer {
    
    width: 100%;
    justify-content: center;
    display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 10px;
      
     
    .challenge-attempt-attempt-row {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      padding: 10px;
      margin-bottom: 4px;
      border-radius: 12px;
      background-color: #00000044;
      padding-top: 10px;
      gap: 20px;
      align-items: center;
    }
  }
  .challenge-attempt-button { 
      border: 4px solid rgb(128, 128, 128);
      border-radius: 12px;
      background-color: #f2efef;
      margin-bottom: 5px;
      transition: all 0.3s ease;
      width: 130px;
      height: 70px;
      font-size: .45em;
      line-height: 1.1em;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  
      .challenge-attempt-button-success {
          font-size: 1.2em;
      }
      .challenge-attempt-exhaustion {
            font-size: .75em;
           
      }
      cursor: pointer;
      &:hover {
          transform: scale(1.1);
      }
      &.disabled {
        opacity: 0.5;
        cursor: auto;
        &:hover {
            transform: none;
        }
    }

      
  }
     .challenge-display-footer-info-row {
          font-size: .5em;
          display: flex;
          flex-direction: row;
          justify-content: space-around;
          align-items: center;
          width: 100%;
         background-color: #00000044;
      }
       .challenge-display-footer-info { 
          margin-top: 8px;
          
          &.success {
              font-size: 25px;
              margin-top: 0px;
              
          }
          &.exhaustion, &.failure {
              color: white;
          }
      }


      .challenge-result-container {
        display: flex;
        flex-direction: column;
        .challenge-result-header {
            color: #b3b3b3;
            font-size: .55em;
        }
        .challenge-result-info {
            display: flex;
            flex-direction: row;
            justify-content: center;
            color: #b3b3b3;
            font-size: .4em;
            .challenge-result-labels {
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                align-items: flex-end;
                margin-right: 10px;
                
            }
            .challenge-result-values {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;
                margin-right: 10px;
            }
        }
        .challenge-result-button {
            border: 4px solid rgb(128, 128, 128);
            border-radius: 12px;
            background-color: #f2efef;
            
            transition: all 0.3s ease;
            width: 120px;
            height: 30px;
            font-size: .55em;
           
            display: flex;
           
            align-items: center;
            justify-content: center;
        
            
            cursor: pointer;
            &:hover {
                transform: scale(1.1);
            }
        }
      }