
:root {
    --chat-display-toggle-height: 50px;
    --chat-background-color: #809aa8;
    --chat-background-color-trans: #9aa9b1;
    --chat-expanded-background-color: #9aa9b1e9;
    --chat-color-alt: #81aee3;
    --chat-button-color: #6b7e9f;
    --chat-button-color-alt: #566a71;
}




.chat-exterior {
     position: relative;
    
     max-height: 100%;
     width: 100%;
     box-sizing: border-box;
     min-width: 520px;
     margin: 4px;
     margin-left: 8px;
     &.expanded{
        top: -500px;
        z-index: 100;
     }
    
    .chat-container {
        display: flex; 
        
        flex-direction: row;
        height: 100%;
        width: 100%;
        position: relative;
        box-sizing: border-box;
        background-color: var(--chat-background-color);
        border-radius: 10px;
        .new-message-container {
            display: flex;
            flex-grow: 1;
            
            height: 100%;
            max-height: 100%;
            border-radius: 10px;
            .chat-muted-message{
                color: var(--chat-background-color-trans);
                font-size: 5rem;
                font-weight: bold;
                height: 120px;
                margin-left: 30px;
            }
            .chat-new-message {
                width: 100%;
                background-color: var(--chat-background-color-trans);
                color: white;
                height: 100%;
                
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;;
          
                    .chat-message-display{
                        display: flex;
                        flex-direction: row;
                        gap: 10px;
                        align-items: center;
                        
                        justify-content: space-between;
                      
                        box-sizing: border-box;
                        height: 100%;
                        border-radius: 10px;
                        .chat-message-speaker-container {
                            
                            flex-direction: column;
                            display: flex;
                           
                            justify-content: center;
                            width: 100px;
                            align-items: center;
                            background-color: var(--chat-color-alt);
                            border: 4px solid var(--chat-button-color);
                            height:100%;
                            box-sizing: border-box;
                            border-radius: 10px;
                            max-height: 100%;
                            .chat-message-speaker {
                                color: white;
                                display: flex;
                                font-size: large;
                           //     animation: fadeOut 5s forwards;
                            }
                            .chat-message-icon{
                           //     animation: fadeOut 5s forwards;
                                width: 80px;
                                height: 80px;
                                display: flex;
                                margin: 0;
                                background-color: var(--chat-button-color);
                              
                                overflow: hidden;
                                box-sizing: border-box;
                                .cimage {
                                  
                                    position: relative;
                                    display: flex;
                                    border-radius: 5px;
                                    width: 100%;
                                    ;
                                }
                            }
                        }
                        .chat-messager-content {
                       //  border: 2px solid pink;
                           margin-right: 10px;
                           width: calc(100% - 100px);
                            .chat-message-speaker {
                                display: none;
                            }
                        }
                        .chat-message-text{
                            overflow: hidden;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            height: 100%;
                           
                            .chat-message-words {
                               
                          padding: 4px;
                                position: absolute;
                                font-size: 1.4em;
                                line-height: 1em;
                                font-weight: bold;
                                
                            }
                            display: flex;
                            border-radius: 15px;
                            border: 3px solid #00000055;
                            background: white;
                            width: 100%;
                            position: relative;
                            color: var(--notebook-text-color);
                            box-sizing: border-box;
                        }
                    }
                }
        }
    


        .chat-controls {
            background-color: var(--chat-background-color-trans);
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            gap: 0px;
            border-top-right-radius: 10px;;
            border-bottom-right-radius: 10px;;
            
            .chat-toggle{
                   display: none;
                    border-radius: 10px;
                    border: 3px solid var(--chat-background-color);
                    background-color: var(--chat-button-color);
                    width: 50px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    font-size: 2em;
                    font-weight: bold;
                    color: #ffffffa8;
                    cursor: pointer;
                    margin-right: 4px;
                    .chat-toggle-text {
                        padding-bottom: 4px;
                        
                        &.open {
                            transform: scale(2, 1);
                        }
                    }
                    .chat-toggle-icon {
                       
                       
                        .chat-messages-count-container {
                                display: flex;
                                flex-direction: row;
                                justify-content: center;
                                align-items: center;
                                position: relative;
                                .chat-toggle-icon-text {
                                    color: white;
                                    font-weight: bold;
                                }
                            }
                        }
                }
                
            }
        }

}


.chat-message-display {
    display: flex;
    flex-direction: row;
    max-height: 100%;
    .chat-message-speaker-container {
        .chat-message-speaker {
            &.under-icon {
              
                color: black;
               
            }
        }
    }
    .chat-message-icon{
        width: 150px;
        height: 150px;
        border-radius: 50%;
   
        margin: 10px;
        .chat-message-speaker {
            display: none;
        }
    }
    .chat-messager-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        .chat-message-speaker {
            font-weight: bold;
            font-size: 1.5em;
            margin-bottom: 4px;
            display: static;
        
        }
        .chat-message-text{
            border-radius: 5px;
            background: white;
            padding: 2px;
            min-height: 100px;
            width: 100%;
            max-height: 100%;
            color: var(--notebook-text-color);
            font-size: 1em;
        }
    }
}

.chat-expanded {
    position: absolute;
    background-color: var(--chat-expanded-background-color);
    border-radius: 5px 5px 0 0;
    z-index: 100;
    width: 100%;
    .chat-expanded-container {
        transition: 1s;
        max-height: 500px;
        overflow-y: scroll;
        overflow-x: hidden;
        scrollbar-width: thin; /* For Firefox */
        scrollbar-color: var(--chat-button-color) transparent; /* For Firefox */
        &::-webkit-scrollbar {
            width: 8px; /* For WebKit browsers */
            height: 0; /* Hide horizontal scrollbar */
        }
        &::-webkit-scrollbar-thumb {
            background-color: var(--chat-button-color);
            border-radius: 10px;
        }
        &::-webkit-scrollbar-track {
            background: transparent;
        }
        &::-webkit-scrollbar-thumb {
            background-color: var(--chat-button-color);
            border-radius: 10px;
        }
        &::-webkit-scrollbar-thumb:hover {
            background: var(--chat-button-color-alt);
        }
        &::-webkit-scrollbar-thumb:vertical {
            border-left: 2px solid transparent; /* Create space on the left */
        }
        .chat-expanded-messages {
            display: flex;
            flex-direction: column;
            margin-top: 10px;
           justify-content: center;
           
            .chat-message-container{
                margin-bottom: 10px;
                flex-direction: row;
                font-size: 2em;
                .chat-message-display{
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    .chat-message-speaker-container {
                        display: flex;
                        flex-direction: column;
                        align-items: left;
                        
                        .chat-message-speaker {
                            font-weight: bold;
                            font-size: .5em;
                            margin-bottom: 0px;
                            color: black;
                            line-height: 1em;
                            white-space: nowrap;


                        }
                        .chat-message-icon{
                            width: 50px;
                            height: 50px;
                            border-radius: 50%;
                        
                            margin: 10px;
                            .cimage {
                                border-radius: 5px;
                            }
                        }
                    }
                    .chat-messager-content {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        width: 100%;
                        .chat-message-speaker {
                            display: none;
                        }
                    }
                    .chat-message-text{
                        border-radius: 5px;
                        background: white;
                        padding: 10px;
                        min-height: 50px;
                        width: calc(100% - 50px);
                        color: var(--notebook-text-color)
                    }
                }
                
                &:nth-child(odd) { 
                    .chat-message-display {
                    flex-direction: row-reverse; // Swap the order for odd messages
                    margin-left: 50px;
                    margin-right: 0px;
                    .chat-message-icon{
                        margin-left: -5px;
                        margin-right: 10px;
                    }
                    .chat-message-speaker{
                        margin-left: -5px;
                    }
                  }
                }

                &:nth-child(even) { 
                    .chat-message-display {
                        margin-right: 50px;
                        .chat-message-icon{
                            margin-left: 10px;
                            margin-right: -5px;
                        }
                        .chat-message-speaker{
                            margin-left: 10px;
                        }
                    }
                }
            }
        }
    }
}

