.nav-button {
  
	margin-top: 20px;
	
	padding: 14px;
    height: 140px; /* Ensure the height matches the footer height */
    width: 140px; /* Ensure the width matches the height for round buttons */
    border-radius: 100%;  
    border-style: solid;
    color: white;
	border-color: grey;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
    background-color: #ffffff00;
    cursor: pointer;
    transition: .5s;
    &.highlight {
        animation: pulsate .8s infinite; // Apply the pulsate animation
        
        border-width: 5px;
        border-color: white;
      }
    &:hover {
      transform: scale(1.1);
      border-width: 10px;
      border-color: white;
    }

  }

  .text-pill {
    border-radius: 15px;
    height: fit-content;
    width: fit-content;
    position: relative;
    height: 24px;
    top: 4px;
    white-space: nowrap;
    padding: 0px 8px; // Adjust padding to make the pill smaller
    margin: 2px 15px; // Use negative margins to offset the padding
    &.hate {
        background-color: #e8868667;
        border: 2px solid #92555567;
    }
    &.like {
        background-color: #86e8a667;
        border: 2px solid #55925667;
    }
    &.trait {
        display: inline-block; 
        background-color: #d386e867;
        border: 2px solid #79559267;
    }
    &.tag {
        background-color: #86d3e867;
        border: 2px solid #557f9267;
    }
    &.branch {
        background-color: #86e8d367;
        border: 2px solid #55926b67;
    }
    &.expected {
        background-color: #ffffff00;
        border: 2px solid white;
    }

    &.combatant {
      background-color: #91a8aff6;
      border: 2px solid rgb(53, 53, 39);
      &.enemies{
        background-color: rgb(189, 79, 79);;
      }
    }

    &.established {
      background-color: rgb(255, 255, 255);
      color: rgb(33, 33, 33);
      border-color: black;
  }
    .notebook-text {
        top: -2px;
        left: 1px;
    }

    &.player-like {
      color: rgb(148, 192, 148);
    }
    &.player-hate {
      color: rgb(210, 150, 150);
    }
    &.player-conflicted {
      color: rgb(65, 65, 124);
    }
   
      
    &:hover .tooltip {
      opacity: 1;
      transform: translateY(-5px);
    }
  
   

  }




 .tooltip {
      position: absolute;
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%) translateY(0);
      padding: 8px;
      background: rgba(0, 0, 0, 0.8);
      border-radius: 4px;
      font-size: 0.9em;
      opacity: 0;
      transition: all 0.3s ease;
      pointer-events: none;
      white-space: nowrap;
      z-index: 1000;
      color: white;
      &::after {
        content: '';
        position: absolute;
        top: 100%;
        left: 5%;
        margin-left: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: rgba(0, 0, 0, 0.8) transparent transparent transparent;
      }
    }
    .theme-wrapper {
      position: relative;
      &:hover .tooltip {
        opacity: 1;
        transform: translateY(-5px);
      }
    }
  .text-encounter {
    
    color: rgb(0, 0, 0);
  
    padding-left: 10px;
