  .circle-display {
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;
  }
  
  .circle {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    border: 3px solid black;
    
    &.filled {
      background-color: var(--notebook-text-color);
    }
  }


  .fill-bar {
    .fill-bar-filled-button {
      width: 100%;
      font-weight: bold;
      cursor: pointer;
      background-color: black; 
      color: white;
      transition: 1s;
     
      &:hover {
        background-color: white; 
        color: black;
        transform: scale(1.1);
      }
    }

    .fill-bar-container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      height: 20px;
      border-radius: 10px;
      overflow: hidden;
      margin: 2px 0;
      position: relative;
      .fill-bar-label-over {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
        pointer-events: none;
        font-weight: bold;;
      }
    }
    
    .fill-bar-segment {
      flex: 1;
      height: 100%;
      border-right: 1px solid #fff; // Segment lines
      transition: background-color 0.3s;
      &.dark {
        border-right: 1px solid #333; // Segment lines
      }
    
      &:last-child {
        border-right: none; // Remove border from the last segment
      }
      &.theme-{
        background-color: var(--town-display-pencil-light);
        &.filled, &.dark {
          background-color: var(--town-display-pencil);
        }
      }
    }

    .fill-bar-label {
      font-size: 1.24em;
      font-weight: bold;
      text-transform: uppercase;
      top: -20px;
     
    }
  }
 
  .number-die {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #f0f0f0;
    color: #000;
    font-size: 2em;
    font-weight: bold;
    border: 1px solid #ccc;
    cursor: pointer;
    transition: .5s;
    .number-die-text {
        font-size: .8em;
        position: absolute;   
        color: gold;
        margin-left:-6px; 
    }
    &:hover {
        background-color: #3f51b5;
        color: white;
       
    }
  }


